@import "./Variables.scss";


.projects{
  width: 100%;
  height: 74%;
 // border: 2px solid red;
  background-color: $arcticGrey;
  position: relative;

  .projects-container{
    width: 90%;
    height: 90%;
    margin: 20% auto;
   // border: 2px solid blue;



    .project:nth-child(1){
      background: linear-gradient(279deg, rgb(75, 207, 250) 23%, rgb(25, 159, 249) 98.9%);

      .project-desc {
      color: $arcticGrey;
      }

      .preview-container{
        box-shadow: none;
        background-color: transparent;

        .preview-img{
          box-shadow: none;
        }
      }
    }
    .project:nth-child(1).darkmode{
      background: radial-gradient(circle at 10% 20%, rgb(7, 121, 222) 0%, rgb(20, 72, 140) 90%);
    }

    .project:nth-child(2){
      background: linear-gradient(to top, #0ba360 0%, #3cba92 100%);

      .preview-container{
        background-color: transparent;

        .preview-img{
          box-shadow: none;
        }
      }
    }
    .project:nth-child(2).darkmode{
      background: #093028;  /* fallback for old browsers */
      background: -webkit-linear-gradient(to right, #237A57, #093028);  /* Chrome 10-25, Safari 5.1-6 */
      background: linear-gradient(to right, #237A57, #093028); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
      .project-desc {
        color: $arcticGrey;
        }

      
      
    }

    .project:nth-child(3) {
      background: rgb(37,54,75);
      background: linear-gradient(109deg, rgba(37,54,75,1) 16%, rgba(20,36,55,1) 93%);
    } 
    .project:nth-child(3).darkmode {
      background: rgb(37,54,75);
      background: linear-gradient(109deg, rgba(37,54,75,1) 16%, rgba(20,36,55,1) 93%);
    }

    .project:nth-child(4) {
      background: #ECE9E6;  /* fallback for old browsers */
      background: -webkit-linear-gradient(to right, #FFFFFF, #ECE9E6);  /* Chrome 10-25, Safari 5.1-6 */
      background: linear-gradient(to right, #FFFFFF, #ECE9E6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
      
      .project-desc {
        color: $arcticGrey;
        }

        
    } 
    .project:nth-child(4).darkmode {
      background: #8A2387;  /* fallback for old browsers */
      background: -webkit-linear-gradient(to right, #F27121, #E94057, #8A2387);  /* Chrome 10-25, Safari 5.1-6 */
      background: linear-gradient(to right, #F27121, #E94057, #8A2387); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
      opacity: 0.8;

     
    }

    .project:nth-child(5) {
      background: #485563;  /* fallback for old browsers */
      background: -webkit-linear-gradient(to right, #29323c, #485563);  /* Chrome 10-25, Safari 5.1-6 */
      background: linear-gradient(to right, #29323c, #485563); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
      
    }

    .project:nth-child(6){
      background-color: #1a1a1d;

      .preview-container {
        background-color: inherit;
        box-shadow: none;
       

        .preview-img{
          background-color: inherit;
          box-shadow: none;
         
        }
      }
    }
  }




  











  .project-wave{
    position: absolute;
    bottom: 0;
    
  
    @media (min-width: 920px){
      top: 114%;
    }
    @media (min-width: 1025px){
      top: 112%;
    }
  }
  
  .project-wave.darkmode{
    path{
        fill: $nordDarkerGrey;
    }
  }


}

.projects.darkmode{
  background-color: $nordGrey;
}



