@import "./Variables.scss";

*,
*:before,
*:after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}



body{
    width: 100vw;
    height: 100vh;
}

.navbar{
  background-color: $white;
    width: 100vw;
    height: 3%;
    display: flex;
    justify-content: space-evenly;
    align-items: baseline;
    position: relative;
    top: 0;
    font-family: $mainFont;
    //border-bottom: 2px solid $applegrey;
    
   

   

    .logo{
        
       // background-image: url("../Assets/Portfolio\ Logo\ LightMode.svg");
      
        background-color: $applegrey;
        opacity: 0.8;
        backdrop-filter: saturate(180%) blur(20px);
        background-size: contain;
        background-repeat: no-repeat;
        width: 10rem;
        border-radius: 0.2rem;
        height: 3.4rem;
        position: absolute;
        top: 12%;
        left: 3%;
        order: 1;

       

        .logo-img{
          position: absolute;
          background-image: url("../Assets/Ronan\ Goligy.logo.svg");
          background-size: contain;
          background-repeat: no-repeat;
          top: 5%;
          left: 5%;
          width: 10rem;
          height: 3rem;
          
        }
        .logo-img.darkmode{
          background-image: url("../Assets/Ronan\ Goligy.logo.dark.svg");
        }
    }
    .logo.darkmode{
      background-color: $nordGrey;
    }

    .desk-menu {
        @media (min-width: 600px) {
            width: 50%;
            height: 2.5rem;
            order: 2;
            display: flex;
            justify-content: space-around;
            list-style: none !important;
           // border: 2px solid red;
            outline: none;
            position: absolute;
            top: 15%;
            right: 0;
            z-index: 11;

          
           

            .dark{
              position: relative;
              top: 0;
              right: 5%;
              width: 2rem;
              height: 2rem;
              border-radius: 0.2rem;
              background-color: transparent;
              outline: none;
              border: none;
              cursor: pointer;
              overflow: hidden;
              color: inherit;

             

              .colour-mode-img{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border-radius: 0.2rem;
               // border: 1px solid inherit;
                outline: none;
                background-image: url("../Assets/sun.svg");
                background-size: contain;
                background-repeat: no-repeat;
                
              }

              

              .colour-mode-img.darkmode{
                background-image: url("../Assets/moon.svg");
                color: white;
              }

             
            }

            .dark:hover{
              background-color: $applegrey;
            }
            .dark:focus {
              outline: none;
            }

           

          
    
            li {
              font-family: "Inter", sans-serif;
              font-weight: 400;
              padding: 0.4rem;
              border-radius: 0.2rem;
              border: none;
              outline: none;
              font-size: 1rem;
              color: $darkGreyFont;
              &:hover {
                background-color: $applegrey;
             
                opacity: 0.8;
                
              }
              @media (min-width: 1100px) {
                font-size: 1rem;
              }
              @media (min-width: 1600px) {
                font-size: 1.2rem;
              }
            }
            a {
              text-decoration: none;
            }


          
          }

          @media (min-width: 920px){
            width: 40%;
            top: 20%;
            
            
          }

          @media (min-width: 1025px){
            width: 30%;
          }
    }
}

.navbar.darkmode{
  background-color: $nordDarkerGrey;

  li{
    color: $arcticGrey;
  }
}

