@import "./Variables.scss";


.project{
  width: 1300px;
  height: 510px;
  margin: 8% auto;
  background-color: white;
  border-radius: 1.1rem;
  position: relative;


  .project-title{
    font-weight: 800;
    font-size: 2rem;
    position: absolute;
    top: 8%;
    left: 2%;
   // border: 2px solid red;
    color: $white;

  }

  .project-desc{
    max-width: 50%;
    font-size: 0.8rem;
    position: absolute;
    top: 20%;
    left: 2%;
    //border: 2px solid red;
    font-size: 1.2rem;
    color: $steel;

  }

  .project-tech{
    position: absolute;
    top: 40%;
    left: 2%;


    li{
      display: inline-block;
      padding: 0.5rem;
      margin: 1%;
      border-radius: 0.7rem;
      color: $steel;
      background-color: $arcticGrey;
      opacity: 1;
    }
   
  }

  .project-tech.darkmode{
    li{
      color: $applegrey;
      background-color: $nordGrey;
      
    }
  }

  .preview-container{
    width: 550px;
    height: 450px;
    //border: 2px solid red;
    border-radius: 0.5rem;
    background-color: $arcticGrey;
    position: absolute;
    top: 5%;
    right: 5%;

    .preview-img{
      position: absolute;
      width: 80%;
      height: 80%;
      top: 10%;
      left: 10%;
      box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
     // border: 1px solid blue;
      object-fit: fill;
      border-radius: 0.5rem;

    }
  }
  



  .demo, .more-info{
    width: 7rem;
    height: 3rem;
    border-radius: 25rem;
    border: 1px solid white;
    color: white;
    background-color: inherit;
    cursor: pointer;
    position: absolute;
    top: 80%;
   

    &:hover{
      background-color: $arcticGrey;
      color: grey;
    }
  }

  .demo.darkmode{
    &:hover{
      border: none;
      color: $arcticGrey;
      background-color: $nordGrey;
    }
  }
  .more-info.darkmode{
    &:hover{
      border: none;
      color: $arcticGrey;
      background-color: $nordGrey;
    }
  }
  
  .demo {
    left: 2%;
  }
  .more-info{
   left: 12%
  }

  
  
}