@import "./Variables.scss";



  .contact {
    background-color: $white;
    width: 100vw;
    height: 100vh;
    position: fixed;
    overflow-y: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    font-family: "Source Code Pro", monospace;
   // border: 2px solid red;
    

    @media (min-width: 1800px){
      height: 2000px;

    }

    .nav{
        width: 100vw;
        height: 10%;
        order: 1;
        display: flex;
        justify-content: space-evenly;
        align-items: baseline;
        position: relative;

        .logo-container{
          width: 10rem;
          height: 3.4rem;
          position: absolute;
          top: 14%;
          left: 3%;
          border-radius: 0.2rem;
          background-color: $applegrey;
          opacity: 0.8;
          backdrop-filter: saturate(180%) blur(20px);
          background-size: contain;
          background-repeat: no-repeat;

          .logo {
            background-image: url("../Assets/Ronan\ Goligy.logo.svg");
            background-size: contain;
            background-repeat: no-repeat;
            width: 10rem;
            height: 3rem;
            position: absolute;
            top: 5%;
            left: 5%;
            order: 1;
          }
  
          .logo.darkmode{
            background-image: url("../Assets/Ronan\ Goligy.logo.dark.svg");
          }

        }
        .logo-container.darkmode{
          background-color: $nordGrey;
        }
    
     
        .close {
          position: absolute;
          width: 6rem;
          height: 2.5rem;
          top: 25%;
          right: 10%;
          cursor: pointer;
          outline: none;
          border: 2px solid $muteRed;
          background-color: $white;
          color: $muteRed;
          border-radius: 0.4rem;
    
          i {
            padding: 0.2rem 0.2rem;
          }
          &:hover {
            color: $white;
            background-color: $muteRed;
          }

          @media (min-width: 1800px){
            width: 7rem;
            height: 3rem;
            right: 5%;
          }
        }

        .close.darkmode{
          background-color: $darkRed;
          opacity: 0.8;
          border: $darkRed;
          color: $white;


          &:hover{
           background-color: $nordDarkerGrey;
            color: $darkRed;
          }
        }

    }

    .contact-main{

        width: 100vw;
        height: 90%;
        order: 2;
       // border: 2px solid green;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    
       .contact-speech-bubble {
          position: absolute;
          top: 20%;
          left: 10%;
          font-size: 4rem;
          opacity: 0.8;
          //color: $ice;
    
          @media (min-width: 920px) {
            top: 15%;
            left: 25%;
          }
          @media (min-width: 1800px){
            font-size: 7rem;
          }
        }
        
    
        .contact-title {
          font-family: "Roboto Condensed", sans-serif;
          font-weight: 600;
        //  line-height: 46px;
          position: absolute;
          top: 20%;
          left: 35%;
          font-size: 3.5rem;
          color: $darkGreyFont;
    
          @media (min-width: 920px) {
            top: 17%;
            left: 33%;
          }

          @media (min-width: 1800px) {
            font-size: 5.5rem;
            top: 27%;
            left: 30%;
          }
        }
        .contact-title.darkmode{
          color: $white;
        }
    
        .contact-message {
          font-family: "Inter", sans-serif;
          height: 7%;
          position: absolute;
          top: 45%;
          left: 10%;
          padding: 0.5rem;
          border-left: 4px solid $applegrey;
          font-size: 1rem;
    
          @media (min-width: 700px) {
            top: 30%;
          }
    
          @media (min-width: 920px) {
            top: 25%;
            left: 25%;
            padding: 1rem;
          }
          @media (min-width: 1400px) {
            top: 30%;
            padding: 1rem;
          }
          @media (min-width: 1800px){
            top: 38%;
          }

        }
        .contact-message.darkmode{
          color: $white;
        }
    }

}

.contact.darkmode{
  background-color: $nordDarkerGrey;
}
 