@import "./Variables.scss";

.contact-form-container{
    
   

    .contact-form{
        width: 80%;
        height: 250%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: stretch;
        position: absolute;
        top: 60%;
        left: 15%;
        //border: 2px solid red;
        padding: 0.5rem;
        //background-color: rgba(236, 239, 244, 0.4);
        background-color: $applegrey;
        opacity: 0.8;
        backdrop-filter: saturate(180%) blur(20px);
        border-radius: 0.7rem;
        
  
        @media (min-width: 920px) {
          width: 45%;
          height: 55%;
          top: 40%;
          left: 25%;
        }

        @media (min-width: 1800px){
          top: 50%;
        }

        .contact-form-element{
            width: 70%;
            height: 10%;
            margin: 0 auto;
            position: relative;
    
            i {
              position: absolute;
              top: 35%;
              right: 5%;
              visibility: hidden;
            }

            .name .error{
              border: 2px solid red;
             
            }
        }


       input,
      textarea {
        width: 100%;
        height: 100%;
        margin: 0 auto;
        outline: none;
        border: 1px solid $nordGrey;
        border-radius: 0.5rem;
        background-color: inherit;
        padding: 0.5rem;
        color: $lightGreyFont;
      }
      input.darkmode, textarea.darkmode{
        border: 1px solid $applegrey;
        color: $applegrey;
      }

      input {
        input::placeholder {
          color: $lightGreyFont;
        }
      }

      textarea {
        resize: vertical;
        color: $lightGreyFont;
        max-height: 150px;

        textarea::-webkit-input-placeholder,
        textarea::-moz-placeholder {
          color: $lightGreyFont;
        }
      }

      .contact-submit {
        width: 8rem;
        height: 3rem;
        margin: 0 0 0 10%;
        background-color: $ice;
        opacity: 0.9;
        border: none;
        outline: none;
        cursor: pointer;
        color: $white;
        font-size: 1.2rem;
        font-weight: 600;
        &:hover {
          opacity: 1;
        }
      

      


    }
 }

 .contact-form.darkmode{
  background-color: $nordGrey;
 }

  p{
    color: $muteRed;
  }
}